.bmi-calculator {
  max-width: 100%;
  margin: 0 auto;
  padding: 40px;
  scroll-margin-top: 60px;
  background-color: var(--primary-dark);
  border-radius: 15px;
  box-shadow: 0 15px 30px rgba(255, 61, 0, 0.4);
  font-family: 'Roboto', 'Arial', sans-serif;
  color: var(--text-light);
  position: relative;
  z-index: 10;
}

.bmi-calculator h2 {
  color: var(--primary-accent);
  font-size: 2.5rem;
  text-align: center;
  margin-bottom: 30px;
  text-transform: uppercase;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
  letter-spacing: 2px;
}

.calculator-container {
  display: flex;
  flex-direction: column;
  gap: 30px;
}

form {
  width: 100%;
}

.input-group {
  margin-bottom: 20px;
}

label {
  display: block;
  margin-bottom: 8px;
  font-weight: bold;
  color: var(--secondary-accent);
  text-transform: uppercase;
  letter-spacing: 1px;
  font-size: 1rem;
}

input[type="number"], select {
  width: 100%;
  padding: 12px;
  border-radius: 6px;
  border: 2px solid var(--primary-accent);
  background-color: rgba(255, 255, 255, 0.1);
  color: var(--text-light);
  font-size: 1.1rem;
  box-shadow: 0 4px 6px rgba(255, 61, 0, 0.2);
  transition: all 0.3s ease;
  -webkit-appearance: none;
  appearance: none;
}

input[type="number"]:focus, select:focus {
  border-color: var(--secondary-accent);
  box-shadow: 0 0 0 2px rgba(0, 74, 173, 0.5);
}

.radio-group {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
}

.radio-group label {
  flex: 1 0 40%;
  display: flex;
  align-items: center;
  padding: 12px;
  background-color: rgba(255, 255, 255, 0.1);
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.radio-group label:hover {
  background-color: rgba(255, 61, 0, 0.2);
}

.radio-group input[type="radio"] {
  margin-right: 10px;
}

button {
  background-color: var(--primary-accent);
  color: var(--text-light);
  padding: 15px 25px;
  font-size: 1.2rem;
  font-weight: bold;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  width: 100%;
  text-transform: uppercase;
  letter-spacing: 2px;
  transition: all 0.3s ease;
}

button:hover, button:active {
  background-color: var(--secondary-accent);
  transform: translateY(-3px);
  box-shadow: 0 6px 12px rgba(255, 61, 0, 0.5);
}

.results {
  width: 100%;
  padding: 30px;
  border-radius: 10px;
  background-color: rgba(255, 255, 255, 0.1);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3);
}

.results h3 {
  color: var(--secondary-accent);
  font-size: 1.8rem;
  margin-bottom: 20px;
  text-transform: uppercase;
}

.results p {
  font-size: 1.1rem;
  margin-bottom: 15px;
}

.bmi-visualizer, .chart-container {
  width: 100%;
  height: 250px;
  margin-bottom: 30px;
  border-radius: 8px;
  overflow: hidden;
}

.cta-button {
  display: block;
  background-color: var(--secondary-accent);
  color: var(--text-light);
  padding: 15px 30px;
  font-size: 1.2rem;
  font-weight: bold;
  text-transform: uppercase;
  text-decoration: none;
  border-radius: 6px;
  transition: all 0.3s ease;
  text-align: center;
  margin-top: 30px;
  letter-spacing: 2px;
}

.cta-button:hover, .cta-button:active {
  background-color: var(--primary-accent);
  transform: translateY(-3px);
  box-shadow: 0 8px 15px rgba(255, 61, 0, 0.5);
}

@media (min-width: 768px) {
  .bmi-calculator {
    padding: 50px;
  }

  .bmi-calculator h2 {
    font-size: 3rem;
  }

  .calculator-container {
    flex-direction: row;
    gap: 40px;
  }

  form, .results {
    flex: 1;
  }
}